<template>
    <div class="myPay">
        <!-- 头部 -->
           <van-nav-bar
            title="确认订单"
            left-arrow
            @click-left="goBack"
            />
        
        <!-- 到餐时间 -->
        <div class="times">预计到餐 约{{times}}可取</div>
              
        <!-- 送餐地址 -->
          <van-address-list
            :list="list"
            default-tag-text="默认"
            @edit="onEdit('Location')"
            />
        
        <!-- 订单详情 -->
        <div class="indent"> 
            <van-card
                v-for="(Item,index) in Lists"
                :key="index"
                :num="Item.count"
                :price="Item.price"
                :desc="Item.rule"
                :title="Item.name"
                :thumb="Item.small_img"
                :origin-price="Item.price-0+16"
            >
            <template #tags>
                <van-tag plain type="danger">{{Item.drop}}</van-tag>
            </template>
            
            </van-card>
            <p class="delivery"> 配送费<span>(配送费6元)</span><span>￥6</span></p>
            <div class="total">
                    <span>合计</span>
                    <span>￥{{priceObj.ultiDate}}</span>
                    <span>￥{{priceObj.costDate}}</span>
            </div>
        </div>

        <!-- 咖啡钱包 -->
        <div class="way">
            <div class="wallet" @click="ff_maintain">
                <span>咖啡钱包</span>
                <span>无可用</span>
            </div>
            <div class="WeChat" @click="ff_maintain">
                
                <span>支付方式</span>
                <span class="icon-xiangyoubiao iconfont"></span>
                <span>微信支付</span>
                <span class="iconfont icon-weixinzhifu"></span>

            </div>
        </div>
        <div class="require" @click="ff_maintain">
            <span>备注特殊要求</span>
            <span class="icon-xiangyoubiao iconfont"></span>
        </div>

        
        <!-- 底部导航 -->
        <div class="base">
            <p class="price">
                <span>总价￥{{priceObj.costDate}}</span>
                <span>优惠￥{{priceObj.ultiDiffer}}</span>
                <span>实付￥{{priceObj.ultiDate}}</span>
            </p>
            <span class="buts" @click="ff_affirm">去支付</span>
        </div>
    </div>
</template>

<script>
    import '../assets/font/iconfont.css'
    import  '../assets/less/Pay.less'
    export default {
       
        name : 'Pay',
        data() {
            return {
                Lists          : [],
                times          : '10:23',
                list           : [],
                priceObj       : {
                    // 原价
                    costDate   : 0,
                    // 终价
                    ultiDate   : 0,
                    // 终极差价
                    ultiDiffer : 0,
                },
                phone          :  '',
                address        :  '',
                receiver       :  '',
                sids           :  ''
            }
        },
        created() {
            
            // 提示到餐时间
            setInterval(() => {
                 this.ff_times();
            }, 1000);
            
           


            if(this.useId){
                 this.sids = this.$route.query.sids;
                 
                 let sids = this.sids;
                  
               
                this.ff_Shopcart(sids);

               //    请求我的信息
                this.axios({
                    url      : "/findAddress",
                    method   : 'GET',
                    params   : {
                        appkey      : this.appkey,
                        tokenString : this.useId,
                    }
                })
                .then(res=>{
                    if(res.data.code == 20000){
                        for(let i=0;i<res.data.result.length;i++){
                            let Item = res.data.result[i];
                          if(Item.isDefault == 1){
                                  let a = Item;
                                  a.address = `${Item.city}${Item.county} (${Item.addressDetail})`
                                  this.list.push(a);
                                  this.phone    = Item.tel;
                                  this.address  = `${Item.city}${Item.county} (${Item.addressDetail})`;
                                  this.receiver = Item.name;
                                  break;
                          }
                        }
                         
                        
                    }
                   
                })
                .catch(err=>{
                     
                })
              
               
               
                
            }else{
                this.$toast('请先登录');
                this.$router.push({name:'Login'});
             }

        
        setTimeout(()=>{
            // 结算
            this.sum();
        },500)
            
            
        },
        methods: {
            goBack(){
                this.$router.go(-1);
            },
            // 到餐时间
            ff_times(){
                let date    = new Date();
                let Hours   = date.getHours();
                let Minutes = date.getMinutes()+25;
                if(Minutes>=60){
                     Minutes = Minutes-0 - 60;
                     Minutes = Minutes < 10 ? `0${Minutes}` : Minutes;
                     Hours   = Hours + 1;
                     Hours   = Hours > 23 ? 0 : Hours;
                     Hours   = Hours < 10 ? `0${Hours}` : Hours;
                }
                this.times = `${Hours}:${Minutes}`
               
            },
            // 修改地址
            onEdit(name) {
                 this.$router.push({name})
            },
            // 查询购买的商品
            ff_Shopcart(sids){
                this.axios({
                    url    : '/commitShopcart',
                    method : 'GET',
                    params : {
                        appkey: this.appkey,
                        tokenString: this.useId,
                        sids,
                    }
                })
                .then(res=>{
                  
                    if(res.data.code == 50000){
                           let a = res.data.result;
                           a.forEach(Item=>{
                               Item.drop = '商品直降'
                           });
                           this.Lists = a;
                            
                    }
                })
                .catch(err=>{
                     
                })
            },
            // 结算
            sum(){
                 
                let costDate = 0;
                let ultiDate = 0;
                this.Lists.forEach(Item=>{
                   costDate   += (Item.price-0+16) * (Item.count-0);
                   ultiDate   += (Item.price-0) * (Item.count-0);
                });
                 
                this.priceObj.costDate     = costDate+6;
                this.priceObj.ultiDate     = ultiDate;
                this.priceObj.ultiDiffer   = costDate - ultiDate;
                 
            },
            // 立即支付
            ff_affirm(){
                 this.axios({
                    url    : "/pay",
                    method : 'POST',
                    data   : {
                        appkey      : this.appkey,
                        tokenString : this.useId,
                        sids        : this.sids,
                        phone       : this.phone,
                        address     : this.address ,
                        receiver    : this.receiver,
                    }
                })
                .then(res=>{
                    if(res.data.code == 60000){
                         this.$toast('支付成功');
                         setTimeout(()=>{
                             this.$router.push({name:'Order'});
                         },300)
                    }
                    
                    
                })
                .catch(err=>{
                     
                })
                
            },
            ff_maintain(){
                this.$toast.loading({
                            message: '维护中...',
                            // 禁止点击背景
                            forbidClick: true,
                            loadingType: 'spinner',
                });
            }
            
        },
    }
</script>

<style lang="less" scoped>

</style>